.collection-item {
  width: 22vw;
  display: flex;
  flex-direction: column;
  height: 350px;
  align-items: center;
  position: relative;
  margin: 5px;

  .image {
    width: 100%;
    height: 95%;
    background-size: cover;
    background-position: center;
    margin-bottom: 5px;
  }

  .custom-button {
    width: 80%;
    opacity: 0.7;
    position: absolute;
    top: 255px;
    display: none;
  }

  &:hover {
    .image {
      opacity: 0.8;
    }

    .custom-button {
      opacity: 0.85;
      display: flex;
    }
  }

  .collection-footer {
    width: 100%;
    height: 5%;
    display: flex;
    justify-content: space-between;
    font-size: 20px;
    margin-left: 10px;
    font-weight: 600;
    color: rosybrown;

    .name {
      width: 70%;
      font-size: 18px;
      margin-bottom: 15px;
    }

    .price {
      text-decoration-line: underline;
      font-size: 20px;
      color: brown;
      width: 20%;
    }
  }
}
